/* Base styles for larger screens */
.headerDiv {
  /* background-color: black; */
  background: linear-gradient(to right, blue, red);
  color: antiquewhite;
  padding: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  position: relative; /* Ensure positioning context for dropdowns */
}

.headerDiv a {
  color: antiquewhite;
  text-decoration: none;
  position: relative;
  padding-bottom: 5px;
}

.headerDiv a::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 2px;
  background: antiquewhite;
  transition: width 0.5s ease;
}

.headerDiv a:hover::after {
  width: 100%;
}

.headerDiv a:hover {
  color: #00ff00;
  text-shadow: 0 0 5px rgba(0, 255, 0, 0.7), 0 0 10px rgba(0, 255, 0, 0.5);
  transform: scale(1.1); /* Adjusted scale for better fit */
}

.links {
  display: flex;
  flex-direction: row;
  gap: 50px;
  width: 100%;
}

.dropdown {
  position: relative;
}

.dropdown-button {
  background: transparent;
  border: none;
  color: antiquewhite;
  cursor: pointer;
  font-size: 18px;
}

.dropdown-menu {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background: rgb(231, 2, 2);
  box-shadow: 0 2px 5px rgba(0,0,0,0.2);
  z-index: 1000;
  min-width: 160px;
  flex-direction: column;
}

.dropdown-item {
  padding: 20px;
  text-decoration: none;
  display: block;
  margin-bottom: 10px;
}

.dropdown-item:hover {
  background-color: #333;
  text-shadow: 0 0 5px rgba(0, 255, 0, 0.7);
}

/* Adjustments for sub-dropdown (if needed) */
.sub-dropdown {
  display: none;
  position: absolute;
  top: 0;
  left: 100%;
  margin-left: 5px;
  background: black;
  border: 1px solid antiquewhite;
  box-shadow: 0 2px 5px rgba(0,0,0,0.2);
  z-index: 1000;
  min-width: 160px;
}

.sub-dropdown-item {
  padding: 10px;
  color: antiquewhite;
  text-decoration: none;
}

.sub-dropdown-item:hover {
  background-color: #333;
}

/* Show dropdown menu when Products button is clicked */
.dropdown .dropdown-menu {
  display: flex; /* Show menu when Products button is clicked */
}

/* Adjustments for mobile views */
@media (max-width: 768px) {
  .dropdown-menu {
    position: static; /* Adjust position for mobile */
  }

  .sub-dropdown {
    position: static; 
    margin-left: 0;
  }
}

/* Responsive styles for smaller screens */
@media (max-width: 768px) {
  .headerDiv {
    flex-direction: column;
    padding: 20px;
  }

  .menu-toggle {
    display: block;
    font-size: 34px;
    cursor: pointer;
  }

  .links {
    display: none;
    flex-direction: column;
    gap: 10px;
    margin: 0;
    width: 100%;
    background-color: black;
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1000;
  }

  .links.show {
    display: flex;
  }
}

@media (max-width: 480px) {
  .headerDiv {
    padding: 15px;
  }

  .links {
    gap: 5px;
  }

  .headerDiv a {
    font-size: 16px;
    margin-left: 14px;
    font-weight: bold;
  }

  .headerDiv a:hover {
    transform: scale(1);
  }
}

.menu-toggle {
  display: none; /* Hidden by default */
  cursor: pointer;
  color: antiquewhite;
}

@media (max-width: 768px) {
  .headerDiv {
    display: flex;
    flex-direction: row;
  }

  .menu-toggle {
    display: block;
    font-size: 34px;
  }

  .links {
    display: none;
    flex-direction: column;
    gap: 10px;
  }

  .links.show {
    display: flex;
  }

  .dropdown-button {
    margin-left: 14px;
    font-weight: bold;
  }
}
