/* Basic Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: 'Roboto', sans-serif;
  }
  
  /* Contact Section Styling */
  .contact-container {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 2rem;
    background-color: #8dc591;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.12);
    border-radius: 12px;
    max-width: 1000px;
    margin: 2rem auto;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    background: linear-gradient(to right, rgb(222, 222, 235), rgb(235, 169, 169));
  }
  
  .contact-container:hover {
    transform: translateY(-5px);
    box-shadow: 0 12px 30px rgba(0, 0, 0, 0.15);
  }
  
  .contact-info {
    flex: 1;
    padding: 1.5rem;
    margin-right: 2rem;
    border-right: 2px solid #eee;
  }
  
  .contact-info h2 {
    color: #e53935;
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 1rem;
    font-family: 'Gill Sans';
  }
  
  .contact-info p {
    color: #555;
    margin-bottom: 1.5rem;
    line-height: 1.5em;
    font-family: 'Gill Sans';
    letter-spacing: 0.1em;
  }
  
  .contact-info ul {
    list-style-type: none;
    padding-left: 0;
    font-family: 'Gill Sans';
  }
  
  .contact-info li {
    margin-bottom: 1.6rem;
    font-size: 1.2rem;
    color: #333;
    line-height: 1.5em;
    letter-spacing: 0.1em;
  }
  
  .contact-info li strong {
    color: #e55b35;
    font-family: 'Gill Sans';
  }
  
  /* Form Styling */
  .contact-form {
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: 1.5rem;
  }
  
  .contact-form input,
  .contact-form textarea {
    width: 100%;
    padding: 0.85rem;
    margin-bottom: 1rem;
    border: 1px solid #ddd;
    border-radius: 8px;
    font-size: 1rem;
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
    background: linear-gradient(to right, rgb(95, 95, 231), rgb(211, 92, 92));
    color: whitesmoke;
  }

  ::placeholder {
    color: white;
    font-weight: bold;
  }
  
  .contact-form input:focus,
  .contact-form textarea:focus {
    border-color: #e53935;
    box-shadow: 0 0 8px rgba(229, 57, 53, 0.2);
    outline: none;
  }
  
  .contact-form textarea {
    resize: vertical;
    min-height: 150px;
  }
  
  .contact-form button {
    padding: 1rem;
    background:  rgb(235, 5, 5);
    color: #abb7f4;
    border: none;
    letter-spacing: 0.6em;
    border-radius: 8px;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 600;
    transition: background-color 0.3s ease, transform 0.2s ease;
  }
  
  .contact-form button:hover {
    /* background-color: #c62828; */
    background: linear-gradient(to right, rgb(95, 95, 231), rgb(211, 92, 92));
    transform: translateY(-2px);
    letter-spacing: 0.3em;
  }
  
  .contact-form button:active {
    background-color: #b71c1c;
    transform: translateY(0);
  }

  .map-container {
    margin-top: 50px;
    text-align: center;
    padding-top: 40px;
    margin-bottom: 50px;
    box-shadow: 0 0 8px rgba(245, 75, 1, 0.7);
  }
  
  .map-container iframe {
    max-width: 100%;
    height: 450px;
    border: none;
    margin-bottom: 50px;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .contact-container {
      flex-direction: column;
      padding: 1rem;
    }
  
    .contact-info {
      margin-bottom: 1rem;
      border-right: none;
      border-bottom: 2px solid #eee;
      text-align: center;
    }
  
    .contact-form {
      width: 100%;
    }
  }
  