/* Base styling */
body {
  font-family: "Montserrat", sans-serif;
  color: #333;
  background-color: #f5f8fa;
  margin: 0;
  padding: 0;
}

/* Container styling */
.aboutContainer {
  margin: 0px auto;
  padding: 40px;
  width: 100%;
  /* background: #ffffff; */
  background: linear-gradient(135deg, #b8d1f4 0%, #f8c2c2 100%);
  /* border-radius: 20px; */
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.4);
  border: 1px solid #e0e0e0;
}

/* Heading styling */
.heading {
  font-family: "Gill Sans";
  text-align: center;
  font-size: 48px;
  font-weight: 600;
  margin: 20px 0;
  letter-spacing: 1px;
  color: #333;
}

.heading span {
  display: inline-block;
  padding: 5px 15px;
  /* padding: 5px 1px; */
  border-radius: 25px;
  background: linear-gradient(to right, #9593d7, #d66c3b);
  color: #fff;
  text-shadow: 1px 1px 3px rgba(62, 15, 233, 0.3);
  font-family: "Gill Sans";
  margin-right: 1px;
}

/* Overview image section */
.overview_image {
  display: flex;
  gap: 30px;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 40px;
}

.overview {
  flex: 1;
  padding: 20px;
}

.overHead {
  font-family: "Playfair Display", serif;
  font-size: 36px;
  font-weight: 700;
  margin-bottom: 20px;
  color: #ff6f61;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3);
  text-align: center;
}

.overPara {
  font-size: 18px;
  line-height: 1.8;
  color: #666;
}

/* Image styling */
.image {
  flex: 1;
  max-width: 500px;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.image img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.image:hover {
  transform: scale(1.05);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
}

/* Vision & Mission section */
.vision_mission {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  gap: 30px;
  margin-top: 60px;
  padding: 30px;
  border-radius: 15px;
  /* background: #f0faff; */
  box-shadow: 1px 3px 5px 12px rgba(109, 90, 129, 0.556);
  animation: fadeInUp 1.5s ease-in-out;
  position: relative;
}

.vision,
.mission {
  flex: 1;
  max-width: 70%;
  padding: 20px;
  border-radius: 15px;
  background: #ffffff;
  /* box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15); */
  position: relative;
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.vision:hover {
  transform: scale(1.03);
  box-shadow: 0 20px 30px rgba(236, 7, 7, 0.836);
}

.mission:hover {
  transform: scale(1.03);
  box-shadow: 0 20px 30px rgba(95, 7, 236, 0.651);
}

.vision:before,
.mission:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 150%;
  height: 150%;
  background: linear-gradient(
    455deg,
    rgba(77, 3, 111, 0.2),
    rgba(71, 209, 12, 0.1)
  );
  opacity: 0.3;
  z-index: 1;
  transform: rotate(-20deg);
  pointer-events: none;
}

.sectionTitle {
  font-family: "Playfair Display", serif;
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 20px;
  text-transform: uppercase;
  border-bottom: 3px solid;
  padding-bottom: 10px;
  color: #333;
  position: relative;
  z-index: 2;
}

.sectionTitle.visionTitle {
  color: #ff6f61;
  border-color: #ff6f61;
}

.sectionTitle.missionTitle {
  color: #1e90ff;
  border-color: #1e90ff;
}

.sectionText {
  font-size: 18px;
  line-height: 1.7;
  color: #666;
  position: relative;
  z-index: 2;
}

/* History & Core Values section */
.history_values {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  gap: 30px;
  margin-top: 60px;
  flex-wrap: wrap;
  padding: 20px;
}

.history,
.coreValues {
  flex: 1;
  padding: 20px;
  border-radius: 15px;
  /* background: #b8d1f4; */
  background: linear-gradient(135deg, #b8d1f4 0%, #f8c2c2 100%);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
  width: 80%;
}

.history .sectionText,
.coreValues .sectionText {
  font-family: "Gill Sans";
}

.valuesList {
  list-style-type: disc;
  margin-left: 20px;
  color: #666;
  font-family: "Gill Sans";
}

.valuesList li {
  margin-bottom: 15px;
}

/* Team & Awards section */
.team_awards {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
  margin-top: 60px;
  flex-wrap: wrap;
  padding: 20px;
}

.team,
.awards {
  flex: 1;
  padding: 20px;
  border-radius: 15px;
  background: #ffffff;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
  width: 80%;
}

.team {
  background: #d6f0fe;
}

.teamImages {
  display: flex;
  gap: 20px;
  margin-top: 20px;
}

.teamImage {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid #ddd;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.awards {
  background: #d6e9fe;
}

/* CTA section */

.space {
  width: 50%;
  margin: 0 auto;
}

.cta {
  padding: 50px;
  background: #ffffff;
  border-radius: 15px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  font-family: "Gill Sans";
  margin-bottom: 5%;
}

.ctaLink {
  color: #1e90ff;
  text-decoration: none;
  font-weight: 600;
  border-bottom: 2px solid #1e90ff;
  transition: color 0.3s ease, border-color 0.3s ease;
}

.ctaLink:hover {
  color: #ff6f61;
  border-color: #ff6f61;
}

/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .heading {
    font-size: 36px;
  }

  .heading span {
    padding: 4px 10px;
  }

  .overview_image {
    flex-direction: column;
    text-align: center;
  }

  .image {
    max-width: 100%;
  }

  .vision_mission {
    flex-direction: column;
    align-items: center;
    padding: 20px;
  }

  .vision,
  .mission {
    max-width: 100%;
    margin-bottom: 20px;
    padding: 15px;
  }

  .sectionTitle {
    font-size: 24px;
    margin-bottom: 15px;
    padding-bottom: 5px;
  }

  .sectionText {
    font-size: 16px;
  }

  .history_values {
    flex-direction: column;
    align-items: center;
    padding: 10px;
  }

  .history,
  .coreValues {
    max-width: 100%;
    padding: 15px;
    margin-bottom: 15px;
  }

  .team_awards {
    flex-direction: column;
    align-items: center;
    padding: 10px;
  }

  .team,
  .awards {
    max-width: 100%;
    padding: 15px;
    margin-bottom: 15px;
  }

  .teamImages {
    flex-direction: column;
    align-items: center;
  }

  .teamImage {
    width: 120px;
    height: 120px;
    margin-bottom: 15px;
  }

  .cta {
    padding: 20px;
  }

  .ctaLink {
    font-size: 16px;
  }
}

@media (max-width: 480px) {
  .heading {
    margin-top: 0px;
    font-size: 50px;
  }

  .heading span {
    padding: 3px 8px;
  }

  .overview {
    width: 120%;
    text-align: left;
  }

  .overview .overPara {
    font-size: 15px;
  }

  .overview_image {
    margin-bottom: 20px;
  }

  .image {
    width: 100%;
  }

  .vision_mission {
    padding: 15px;
  }

  .vision,
  .mission {
    padding: 10px;
  }

  .sectionTitle {
    font-size: 20px;
    margin-bottom: 10px;
    padding-bottom: 5px;
  }

  .sectionText {
    font-size: 14px;
  }

  .history_values {
    padding: 5px;
  }

  .history,
  .coreValues {
    padding: 10px;
    margin-bottom: 10px;
  }

  .team_awards {
    padding: 5px;
  }

  .team,
  .awards {
    padding: 10px;
    margin-bottom: 10px;
  }

  .teamImages {
    margin-top: 10px;
  }

  .teamImage {
    width: 100px;
    height: 100px;
  }

  .space {
    width: 100%;
    margin-top: 30px;
    margin-bottom: 30px;
  }

  .cta {
    padding: 18px;
  }

  .ctaLink {
    font-size: 14px;
  }
}
