.myImg {
    mix-blend-mode: multiply;
    width: 70%;
    margin-bottom: 20px;
    border-radius: 50%;
}

.myImg:hover {
    transform: translateY(-5px); 
    transition: all 0.3s ease-in-out;
    rotate: 360deg;
}

.myLogo {
    border-radius: 50%;
    width: 20%;
    margin-left: 40px;
}

.aboutImage {
    width: 100%;
    border-radius: 10%;
}
