body {
  background-color: rgb(215, 234, 250);
  /* background: linear-gradient(to right, #00ff00, #008000); */
}

.segments {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  margin-bottom: 50px;
}

.segmentText {
  text-align: center;
  font-size: 30px;
  text-transform: uppercase;
  font-weight: 600;
}

.productCategory {
  display: flex;
  gap: 20px;
  margin: 20px;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.category {
  border: 2px solid #e53935;
  width: 30%;
  text-align: center;
  padding: 20px;
  background: #b5b5e4;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  cursor: pointer;
}

.category:hover {
  background: #af9fdb;
  border-color: #023645;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
  transform: translateY(-5px);
  border: none;
}

.category:active {
  background: #fbe0e0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transform: translateY(0);
}

.text {
  margin-bottom: 20px;
  letter-spacing: normal;
  font-size: 26px;
  color: #dd1212;
  font-family: "Gill Sans";
}

.ViewProduct {
  border-radius: 25px;
  border: 2px solid #ff5722;
  padding: 12px 24px;
  background: linear-gradient(145deg, #d3e6f5, #a9c8e8);
  color: #e53935;
  font-size: 1rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
  cursor: pointer;
}

.ViewProduct:hover {
  background: linear-gradient(145deg, #ff5722, #e64a19);
  color: #ffffff;
  border-color: #e64a19;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
  transform: translateY(-2px);
}

.ViewProduct:active {
  background: linear-gradient(145deg, #e64a19, #d32f2f);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transform: translateY(0);
}

/* Existing styles ... */

/* Catalogue Section Styles */
.catalogue-section {
  background-color: #94dfb3;
  padding: 40px 20px;
  text-align: center;
  margin-top: 20px;
  /* Ensure padding is responsive */
  padding: 5vw 4vw;
  margin-bottom: 3%;
  box-shadow: 10px 4px 10px 8px rgba(5, 10, 10, 0.2);
}

.catalogue-content {
  max-width: 800px;
  margin: 0 auto;
}

.catalogue-content h2 {
  font-size: 2rem;
  margin-bottom: 20px;
  color: #333;
  /* Responsive font size */
  font-size: 4vw;
  font-family: "Gill Sans";
  font-weight: normal;
}

.catalogue-content p {
  font-size: 1rem;
  margin-bottom: 20px;
  color: #666;
  /* Responsive font size */
  font-size: 2vw;
  font-family: "Gill Sans";
}

.catalogue-button {
  display: inline-block;
  padding: 10px 20px;
  font-size: 1rem;
  color: #fff;
  background-color: #ff2f00;
  border: none;
  border-radius: 5px;
  text-decoration: none;
  transition: background-color 0.3s ease;
  /* Responsive button size */
  padding: 1vw 2vw;
  font-size: 2.5vw;
  font-family: "Gill Sans";
  box-shadow: 10px 4px 10px 18px rgba(5, 10, 10, 0.2);
}

.catalogue-button:hover {
  background-color: #6779e0;
  color: red;
}

.cata {
  background-color: red;
  color: rgb(143, 143, 209);
  border-radius: 10px 50px 10px 30px;
  padding: 10px;
  animation: colorChange 5s infinite;
}

@keyframes colorChange {
  0% {
    color: rgb(235, 124, 13);
  }
  50% {
    color: rgb(183, 255, 100);
  }
  100% {
    color: rgb(228, 13, 13);
  }
}

.red {
  color: rgb(233, 14, 14);
  font-weight: bold;
}

.blue {
  color: rgb(65, 65, 185);
  font-weight: bold;
}

.clients-section {
  padding: 40px 20px;
  text-align: center;
  background-color: #3dafe4;
  margin-bottom: 90px;
  margin-top: 80px;
}

.clients-section h2 {
  font-size: 2rem;
  margin-bottom: 20px;
  color: #65aadf;
}

.clients-container {
  display: flex;
  justify-content: center;
  gap: 80px;
  flex-wrap: wrap;
}

.client-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.client-item img {
  width: 150px;
  height: 150px;
  object-fit: contain;
  border-radius: 8px;
}

.client-item p {
  margin-top: 10px;
  font-size: 1rem;
  color: #eecb05;
}

/* Animation effects */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.client-item {
  animation: fadeInUp 1s ease-out;
}

.client-item:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .catalogue-section {
    padding: 4vw 3vw;
  }

  .catalogue-content h2 {
    font-size: 5vw;
  }

  .catalogue-content p {
    font-size: 4vw;
  }

  .catalogue-button {
    padding: 1.2vw 2.5vw;
    font-size: 3vw;
  }
}

@media (max-width: 480px) {
  .catalogue-section {
    padding: 3vw 2vw;
  }

  .catalogue-section {
    margin-top: 30px;
    margin-bottom: 30px;
  }

  .catalogue-content h2 {
    font-size: 6vw;
  }

  .catalogue-content {
    margin-top: 20px;
  }

  .catalogue-content p {
    font-size: 5vw;
  }

  .catalogue-button {
    padding: 1.5vw 3vw;
    font-size: 3.5vw;
  }

  .catalogue-button {
    box-shadow: 1px 1px 3px 16px rgba(5, 10, 10, 0.2);
    margin-bottom: 20px;
    margin-top: 5px;
    padding: 15px;
  }
}

/* Keyframes for animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes bounceIn {
  from {
    opacity: 0;
    transform: scale(0.5);
  }
  50% {
    opacity: 1;
    transform: scale(1.1);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

/* Add animation to category */
.category {
  animation: fadeInUp 3s ease-out;
}

.category.visible {
  animation: bounceIn 3s ease-out;
}

/* Add animation to ViewProduct button */
.ViewProduct {
  animation: fadeInUp 1s ease-out;
}

.ViewProduct.visible {
  animation: bounceIn 3s ease-out;
}

/* Adjustments for hover and active states */
.category:hover {
  background: #9fcddb;
  border-color: #023645;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
  transform: translateY(-5px);
}

.category:active {
  background: #fbe0e0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transform: translateY(0);
}

.ViewProduct:hover {
  background: linear-gradient(145deg, #ff5722, #e64a19);
  color: #ffffff;
  border-color: #e64a19;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
  transform: translateY(-2px);
}

.ViewProduct:active {
  background: linear-gradient(145deg, #e64a19, #d32f2f);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transform: translateY(0);
}

@media (max-width: 1024px) {
  .productCategory {
    gap: 60px; /* Reduced gap between categories */
  }

  .category {
    width: 45%; /* Adjusted width to fit more categories in a row on tablets */
  }
}

@media (max-width: 768px) {
  .productCategory {
    gap: 20px; /* Further reduced gap for smaller screens */
    flex-direction: column; /* Stack categories vertically */
  }

  .category {
    width: 100%; /* Full width for each category on smaller screens */
  }

  .segmentText {
    font-size: 24px; /* Smaller text size for smaller screens */
  }
}

@media (max-width: 480px) {
  .segmentText {
    font-size: 20px; /* Even smaller text size for very small screens */
  }

  .category {
    padding: 15px; /* Reduced padding for smaller screens */
  }

  .ViewProduct {
    padding: 10px 20px; /* Smaller button padding */
    font-size: 0.875rem; /* Slightly smaller font size */
  }
}
