/* Import the Gill Sans font */
@import url('https://fonts.googleapis.com/css2?family=Gill+Sans:wght@400;700&display=swap');

.industrial-container {
  font-family: 'Gill Sans', sans-serif;
  padding: 20px;
  text-align: center;
  background-color: #29b9fc;
}

.heading {
  font-size: 2rem;
  margin-bottom: 50px;
  margin-top: 40px;
}

.products {
  display: grid;
  grid-template-columns: repeat(4, 1fr); 
  gap: 50px; 
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
}

.product-card {
  background-color: #f2f4fa;
  border-radius: 8px;
  overflow: hidden;
  opacity: 0;
  transform: translateY(20px); 
  animation: fadeInUp 0.6s forwards;
  box-shadow: 0 4px 8px rgba(237, 7, 7, 0.941);
}

.product-card:hover {
  cursor: pointer;
}

.product-card:nth-child(1) {
  animation-delay: 0.1s;
}

.product-card:nth-child(2) {
  animation-delay: 0.2s;
}

.product-card:nth-child(3) {
  animation-delay: 0.3s;
}

.product-card:nth-child(4) {
  animation-delay: 0.4s;
}

.product-card:nth-child(5) {
  animation-delay: 0.5s;
}

.product-card:nth-child(6) {
  animation-delay: 0.6s;
}

.product-card:nth-child(7) {
  animation-delay: 0.7s;
}

.product-card:nth-child(8) {
  animation-delay: 0.8s;
}

.product-card:nth-child(9) {
  animation-delay: 0.9s;
}

.product-card:nth-child(10) {
  animation-delay: 1s;
}

.product-card:nth-child(11) {
  animation-delay: 1.1s;
}

.product-card:hover {
  transform: scale(1.05);
  box-shadow: 0 4px 8px rgba(237, 7, 7, 0.6);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.product-image {
  width: 100%;
  height: 310px;
  object-fit: cover;
}

.product-name {
  padding: 10px;
  font-size: 1rem;
  color: #f50303;
  /* background-color: #fbfaf8; */
  background-color: rgb(252, 227, 227);
  font-size: larger;
  letter-spacing: 0.1em;
}

/* POPUP */

.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(1, 0, 3, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.popup-content {
  background: white;
  padding: 20px;
  border-radius: 8px;
  width: 90%;
  max-width: 800px;
  max-height: 80vh; 
  overflow-y: auto; 
  position: relative;
  padding: 40px;
}

.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background: red;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 5px 10px;
  cursor: pointer;
}

.popup-content .products {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
}

.popup-content .product-card {
  flex: 1 1 calc(33% - 10px);
  box-sizing: border-box;
  margin-top: 20px;
}

.popup-content .product-image {
  max-width: 100%;
  height: auto;
}

.popup-content .product-name {
  text-align: center;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 1200px) {
  .products {
    grid-template-columns: repeat(3, 1fr); 
  }
}
  
@media (max-width: 900px) {
  .products {
    grid-template-columns: repeat(2, 1fr); 
  }
}
  
@media (max-width: 600px) {
  .products {
    grid-template-columns: 1fr; 
  }
}  
