.carousel-container {
  width: 100%;
  margin: auto;
  overflow: hidden;
  position: relative;
}

.carousel-slide img {
  width: 100%;
  height: 100vh;
  display: block;
}

.slick-slide {
  margin: 0;
}

.slick-dots {
  margin-bottom: 30px;
  color: rgb(216, 209, 24);
}

.slick-dots li button:before {
  color: #c9ea09;
}

.slider {
  height: 100vh;
}

@media (max-width: 768px) {
  .carousel-container {
    border-radius: 50px;
  }

  .slider {
    margin: auto;
    width: 98%;
    margin-top: 10px;
    height: 57vh;
    margin-bottom: 10px;
    border-radius: 50px;
  }
  .carousel-slide img {
    height: 57vh;
    border-radius: 50px;
  }
}
