.footer {
  background: linear-gradient(to right, blue, red);
  color: #fff;
  padding: 20px 0;
  position: relative;
  overflow: hidden;
  font-family: "Gill Sans";
}

.footer-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.footer-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 20px;
  font-size: 20px;
  line-height: 2rem;
}

.footer-section {
  flex: 1;
  min-width: 200px;
}

.footer-section h3 {
  margin-bottom: 10px;
  font-size: 1.2em;
  color: #f2f2f2;
}

.footer-section p {
  font-size: 0.9em;
}

.social-links a {
  color: #f2f2f2;
  text-decoration: none;
  margin-right: 15px;
  transition: color 0.3s;
}

.social-links a:hover {
  color: #79d80c;
}

/* Footer Bottom Styles */
.footer-bottom {
  text-align: center;
  margin-top: 20px;
  border-top: 1px solid #444;
  padding-top: 10px;
  position: relative;
  font-size: 20px;
  line-height: 1.2rem;
}

.footer-bottom p {
  font-size: 0.8em;
  margin: 0;
}

/* Animation for Footer */
.footer:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.1);
  animation: animateBackground 5s linear infinite;
}

@keyframes animateBackground {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .footer-content {
    flex-direction: column;
  }
}
